<template>
  <div class="MonHocToan">
    <raw-loader :mytxt="require('!raw-loader!./JwtTest.vue').default"></raw-loader>
    <h1 class="border-bottom">This is JwtTest page</h1>
    logged:{{ $store.state.logged }}<br>
    msg:{{ msg }}<br>
    <button class="m-1" @click="OnHowAreYou">HowAreYou</button>
    <div v-if="$store.state.logged == 0">
      UserName : <input class="m-1" type="text" v-model="UserName" /><br>
      Password : <input class="m-1" type="text" v-model="Password" /><br>
      <button class="m-1" @click="OnLogin">Login</button>
    </div>
    <div v-else>
      <div>
        <div>{{ $store.state.user }}</div>
        <button class="m-1" @click="OnLogout">Logout</button>
      </div>
      <div class="border">
        <button class="m-1" @click="onGetAll">GetAll</button>
        <button class="m-1" @click="onGetById">GetById</button>
      </div>
    </div>
  </div>
</template>
<script>
import Repository from '@/Repository';

export default {
  name: 'Tpl',
  components: {
  },
  data() {
    return {
      UserName: "admin",
      Password: "admin",
      test: "",
      msgGetListUser: [],
      dsUser: [],
      msg:""
    }
  },
  mounted() {
    let logged = localStorage.getItem("logged") ? 1 : 0;
    this.$store.state.logged = logged;
    this.$store.state.user = localStorage.getItem("user");
  },
  methods: {
    OnLogin() {
      this.Login(this.UserName, this.Password).then(res => {
        /*{"data":{
            "Id":1,"FirstName":"Admin","LastName":"User","Username":"admin","Password":null,"Role":"Admin",
            "Token":"eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1bmlxdWVfbmFtZSI6IjEiLCJyb2xlIjoiQWRtaW4iLCJuYmYiOjE2NzM5NDA2NjYsIm
        */
        //return;
        let token = res.data.Token;
        //alert(token);
        localStorage.setItem("token", token);
        localStorage.setItem("logged", 1);
        localStorage.setItem("user", JSON.stringify(res.data));
        window.location.reload();
      })
    },
    OnLogout() {
      localStorage.removeItem('token');
      localStorage.removeItem('logged');
      localStorage.removeItem('user');
      window.location.reload();
    },
    onGetListUser() {
      this.GetListUser().then(res => {
        // alert(JSON.stringify(res));
        // GetListUser        
        // {"data":{"list":[{"UserId":1,"UserName":"admin","FullName":"Thuc 101","Password":"1234        
        this.dsUser = res.data.list;
      })
    },
    onGetAll() {
      this.GetAll().then(res => {
        alert(JSON.stringify(res));
      })
    },
    onGetById() {
      let user = this.$store.state.user;
      let id = JSON.parse(user).Id;
      this.GetById(id).then(res => {
        alert(JSON.stringify(res));
      })
    },
    OnHowAreYou(){
      this.HowAreYou().then(res=>{
        //alert(JSON.stringify(res));
        this.msg=res;
      })
    },
    async GetById(id) {
      return await Repository.get(`/Users/GetById/${id}`).catch((err) => {
        console.log(err);
      });
    },
    async GetAll() {
      return await Repository.get(`/Users/GetAll`).catch((err) => {
        console.log(err);
      });
    },
    async HowAreYou(){
      return await Repository.get(`/Users/HowAreYou`).catch(err=>{
        console.log(err);
      });
    },
    OnTest() {
      this.Test().then(res => {
        this.test = res;
      })
    },
    async Login(UserName, Password) {
      return await Repository.post(`/Users/authenticate`, { UserName: UserName, Password: Password }).catch((err) => {
        console.log(err);
      });
    },
    async Test() {
      return await Repository.get(`/api/User2/GetAdminData`).catch((err) => {
        console.log(err);
      });
    },
    async MyTest() {
      return await Repository.get(`/api/User2/MyTest`).catch((err) => {
        console.log(err);
      });
    },
    async GetListUser() {
      return await Repository.get(`/api/User2/GetListUser`).catch((err) => {
        console.log(err);
      });
    },
  }
}
</script>
<style scoped>
.mypre {
  font-size: 11px;
}
</style>
